import * as React from "react";
import HeroSlider from "./heroSlider";
import laneImg from "../images/Lane.png";
import seltzerImg from "../images/Seltzer.png";
import sippyImg from "../images/Sippy.png";
import snowLeopardImg from "../images/SnowLeopard.png";
import vivaCiderImg from "../images/VIVA_Cider.png";
import bakerslaneImg from "../images/BakerslaneHero.png";
import karimImg from "../images/KarimHero.jpg";
import tinyImg from "../images/TinyHero.png";

const widt = "350px";
const heig = "400px";
const items = [
  {
    id: 100,
    fileSrc: bakerslaneImg,
    label: "Bakerslane",
    width: widt,
    height: heig,
  },
  {
    id: 102,
    fileSrc: tinyImg,
    label: "Tiny",
    width: widt,
    height: heig,
  },
  {
    id: 101,
    fileSrc: karimImg,
    label: "Karim",
    width: widt,
    height: heig,
  },
  {
    id: 1,
    fileSrc: laneImg,
    label: "Lane",
    width: widt,
    height: heig,
  },
  {
    id: 2,
    fileSrc: seltzerImg,
    label: "Seltzer",
    width: widt,
    height: heig,
  },
  {
    id: 3,
    fileSrc: sippyImg,
    label: "Sippy",
    width: widt,
    height: heig,
  },
  {
    id: 4,
    fileSrc: snowLeopardImg,
    label: "SnowLeopard",
    width: widt,
    height: heig,
  },
  {
    id: 5,
    fileSrc: vivaCiderImg,
    label: "VIVA Cider",
    width: widt + widt,
    height: heig,
  },
];

export default function HeroImageSlider() {
  return (
    <HeroSlider>
      {items.map((item, idx) => {
        return (
          <span
            data-bs-toggle="tooltip"
            data-bs-placement="top"
            data-bs-title="Tooltip on top"
            className="p-2"
            key={idx}
          >
            <img
              alt={item.label}
              src={item.fileSrc}
              style={{ width: "300px", height: "400px" }}
              width={item.widt}
              height={item.heig}
            />
          </span>
        );
      })}
    </HeroSlider>
  );
}
